.slider {
  height: 600px;
  max-width: 100%;
  width: auto;
  position: relative;
  margin-bottom: 100px; }
  .slider .swiper-slide {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  .slider .container--md {
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    right: 0;
    height: inherit;
    text-align: center; }
    .slider .container--md h2 {
      color: white;
      margin-top: 100px;
      margin-bottom: 60px;
      font-size: 75px;
      text-transform: uppercase; }
      @media only screen and (min-width: 320px) and (max-width: 460px) {
        .slider .container--md h2 {
          font-size: 45px; } }
      @media only screen and (min-width: 460px) and (max-width: 767px) {
        .slider .container--md h2 {
          font-size: 45px; } }
    .slider .container--md div {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 30px;
      -webkit-animation: bounce 2s infinite;
              animation: bounce 2s infinite; }
      .slider .container--md div p {
        color: white;
        font-style: italic;
        margin-bottom: 5px; }
      .slider .container--md div i {
        color: white; }
  .slider .swiper-container {
    height: 600px; }
  .slider .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6); }
  .slider img {
    width: 100%;
    object-fit: none; }

.modal {
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 253, 253, 0.7);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 9999999; }
  .modal span {
    position: absolute;
    top: 30px;
    color: white;
    font-size: 42px;
    right: 70px;
    cursor: pointer; }
  .modal .form {
    background: white;
