.stories {
  position: relative;
  display: grid;
  grid-gap: 1.5em;
  grid-template-columns: repeat(3, minmax(100px, 1fr)); }
  @media only screen and (min-width: 320px) and (max-width: 460px) {
    .stories {
      grid-template-columns: repeat(1, minmax(100px, 1fr)); } }
  @media only screen and (min-width: 460px) and (max-width: 767px) {
    .stories {
      grid-template-columns: repeat(1, minmax(100px, 1fr)); } }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    .stories {
      grid-template-columns: repeat(1, minmax(100px, 1fr)); } }
  .stories > div {
    padding: 0px; }
    @media only screen and (min-width: 320px) and (max-width: 460px) {
      .stories > div {
        padding: 0 20px; } }
    @media only screen and (min-width: 460px) and (max-width: 767px) {
      .stories > div {
        padding: 0 20px; } }
    @media only screen and (min-width: 768px) and (max-width: 1024px) {
      .stories > div {
        padding: 0 20px; } }

.story {
  position: relative;
  margin-bottom: 40px;
  text-align: center;
  padding: 0 10px;
  border: solid 1px rgba(250, 26, 26, 0.3);
  background-color: #ffffff;
  border-radius: 7px; }
  .story__dp {
    display: -webkit-flex;
    display: flex; }
    .story__dp p {
      font-size: 36px;
      background-color: #fa1a1a;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      padding: 20px;
      margin: 0 auto;
      margin-top: -40px;
      margin-bottom: 33px;
      color: white; }
  .story__headline {
