@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px); } }

@keyframes bounce {
  0% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px); } }

