@import url("https://fonts.googleapis.com/css?family=Rubik");

.App {
  font-family: "Rubik", sans-serif;
  background-color: #fffdfd;
}
//Media Quieries

$breakpoint-small: 460px;
$breakpoint-mid: 767px;
$breakpoint-med: 1024px;
$breakpoint-large: 1320px;

// /* MIXINS FOR MEDIA QUERIES ----------- */

@mixin screen($size, $type: max, $pixels: 600) {
  @if $size== "small" {
    @media only screen and (min-width: 320px) and (max-width: $breakpoint-small) {
      @content;
    }
  }
  @if $size== "mid" {
    @media only screen and (min-width: $breakpoint-small) and (max-width: $breakpoint-mid) {
      @content;
    }
  }
  @if $size== "med-small" {
    @media only screen and (min-width: $breakpoint-mid + 1) and (max-width: $breakpoint-med) {
      @content;
    }
  }
  @if $size== "med" {
    @media only screen and (min-width: $breakpoint-med + 1) and (max-width: $breakpoint-large) {
      @content;
    }
  }
  @if $size== "large" {
    @media screen and (min-width: $breakpoint-med) {
      @content;
    }
  }
  @if $size== "custom" {
    @media screen and ($type + -width: $pixels + px) {
      @content;
    }
  }
}

//Layouts
.container {
  margin: 0 auto;
  max-width: 1040px;
  // padding: 0 20px;
  width: 100%;
  &--xs {
    max-width: 450px;
  }
  &--sm {
    max-width: 600px;
  }
  &--sd {
    max-width: 770px;
  }
  &--md {
    max-width: 960px;
  }
  &--lg {
    max-width: 1240px;
  }
  &--min-width {
    max-width: 90%;
  }
  &--full-width {
    max-width: 100%;
    padding: 0;
  }
}
.loader__page {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
//Buttons
.btn {
  padding: 15px;
  background-color: #d54040;
  color: white;
  font-size: 15px;
  border: 1px solid #d54040;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
  &--green {
    background-color: #3fc764;
    color: white;
    border: 1px solid #3fc764;
  }

  &:focus {
    outline: none;
  }
}
//Form
.form {
  display: block;
  padding: 0 15px;
  position: relative;
  width: 100%;
  &__label {
    text-transform: uppercase;
    margin-bottom: 5px;
    display: block;
    color: #707070;
    font-weight: 400;
  }
  &__control {
    margin-bottom: 25px;
    position: relative;
  }
  &__bar {
    display: flex;
    @include screen("small") {
      display: block;
    }
    @include screen("mid") {
      display: block;
    }
  }
  &__left {
    width: 49%;
    margin-right: 1%;
  }
  &__right {
    width: 49%;
    margin-left: 1%;
  }
  &__field {
    padding: 12px 15px;
    background-color: #ffffff;
    border-radius: 5px;
    outline: none;
    transition: all 0.3s;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    width: 90%;
    font-size: 14px;
    border: solid 1px rgba(250, 26, 26, 0.3);
    // border: 1px solid #524164;
    box-shadow: 0 3px 6px 0 rgba(20, 67, 123, 0.1);
    transition: color 0.25s;
    appearance: none;

    &:focus {
      border: 1px solid #707070;
      transition: 1s all;
    }

    // &--md {
    //   padding: 12px 15px;
    //   width: 100%;
    //   font-size: 1.6rem;
    // }
    &::placeholder {
      color: #ae9ebe;
    }
  }
}

.slider {
  height: 600px;
  max-width: 100%;
  width: auto;
  position: relative;
  margin-bottom: 100px;

  .swiper-slide {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .container--md {
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    right: 0;
    height: inherit;
    text-align: center;
    h2 {
      color: white;
      margin-top: 100px;
      margin-bottom: 60px;
      font-size: 75px;
      text-transform: uppercase;
      @include screen("small") {
        font-size: 45px;
      }
      @include screen("mid") {
        font-size: 45px;
      }
    }
    div {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 30px;
      animation: bounce 2s infinite;
      p {
        color: white;
        font-style: italic;
        margin-bottom: 5px;
      }
      i {
        color: white;
      }
    }
  }
  .swiper-container {
    height: 600px;
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  img {
    width: 100%;
    object-fit: none;
  }
}
.modal {
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 253, 253, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999999;

  span {
    position: absolute;
    top: 30px;
    color: white;
    font-size: 42px;
    right: 70px;
    cursor: pointer;
    // color: rgba(250, 26, 26, 1);
  }
  .form {
    background: white;
    max-width: 960px;
    width: 90%;
    padding: 40px;
    margin: 0 auto;
    box-shadow: 0 20px 30px 0 rgba(0, 0, 0, 0.05);
    border-radius: 14px;
  }
  .story {
    background-color: white;
  }
}

.stories {
  position: relative;
  display: grid;
  grid-gap: 1.5em;
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  @include screen("small") {
    grid-template-columns: repeat(1, minmax(100px, 1fr));
  }
  @include screen("mid") {
    grid-template-columns: repeat(1, minmax(100px, 1fr));
  }
  @include screen("med-small") {
    grid-template-columns: repeat(1, minmax(100px, 1fr));
  }
  & > div{
    padding: 0px;
    @include screen("small") {
      padding: 0 20px;
    }
    @include screen("mid") {
      padding: 0 20px;
    }
    @include screen("med-small") {
      padding: 0 20px;
    }
  }
}

.story {
  position: relative;
  margin-bottom: 40px;

  text-align: center;
  padding: 0 10px;
  border: solid 1px rgba(250, 26, 26, 0.3);
  background-color: #ffffff;
  border-radius: 7px;
  &__dp {
    display: flex;
    p {
      font-size: 36px;
      background-color: #fa1a1a;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      padding: 20px;
      margin: 0 auto;
      margin-top: -40px;
      margin-bottom: 33px;
      color: white;
    }
  }
  &__headline {
    color: #fa1a1a;
    margin-bottom: 20px;
    font-size: 23px;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__content {
    color: #707070;
  }
  &__more {
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 15px;
    cursor: pointer;
  }
  &__likes {
    p:nth-child(1) {
      color: #fa1a1a;
      font-size: 20px;
      cursor: pointer;
    }
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-8px);
  }
  50% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-8px);
  }
}
